import { isFunction } from "@vue/shared";
import { ReactiveEffect } from "./effect";
import { trackRefValue, triggerRefValue } from "./ref";

export function computed(getterOrOptions) {
  let onlyGetter = isFunction(getterOrOptions);
  let getter = onlyGetter ? getterOrOptions : getterOrOptions.get;
  let setter = onlyGetter ? () => {} : getterOrOptions.set;
  console.log(getter, setter);
  return new ComputedRefImpl(getter, setter); // ËøîÂõû‰∏Ä‰∏™ËÆ°ÁÆóÂ±ûÊÄßref
}
class ComputedRefImpl {
  public _value;
  public effect;
  public dep = new Set();
  constructor(getter, public setter) {
    // Êàë‰ª¨ÈúÄË¶ÅÂàõÂª∫‰∏Ä‰∏™effectÊù•ÁÆ°ÁêÜÂΩìÂâçËÆ°ÁÆóÂ±ûÊÄßÁöÑdirtyÁä∂ÊÄÅ
    this.effect = new ReactiveEffect(
      () => getter(this._value),
      () => {
        // ËÆ°ÁÆóÂ±ûÊÄß‰ª•Êù•ÁöÑÂÄºÂèòÂåñ‰∫ÜÔºåÊàë‰ª¨Â∫îËØ•Ëß¶ÂèëÊ∏≤ÊüìÈáçÊñ∞ÊâßË°å
        triggerRefValue(this);
      }
    );
  }
  get value() {
    if (this.effect.dirty) {
      //ÈªòËÆ§ÂèñÂÄº‰∏ÄÂÆöÊòØdirtyÔºåÊâÄ‰ª•Á¨¨‰∏ÄÊ¨°ÂèñÂÄºÁöÑÊó∂ÂÄô‰ºöÊâßË°åeffect.run()ÔºåÁÑ∂ÂêédirtyÂèò‰∏∫false
      this._value = this.effect.run();
      trackRefValue(this);
      // Â¶ÇÊûúÂú®ÂΩìÂâçeffect‰∏≠ËÆøÈóÆ‰∫ÜËÆ°ÁÆóÂ±ûÊÄßÔºåËÆ°ÁÆóÂ±ûÊÄß‰ºöÊî∂ÈõÜËøô‰∏™effectÔºåÈÇ£‰πàËÆ°ÁÆóÂ±ûÊÄß‰ºöÈáçÊñ∞ÊâßË°åÔºåÂπ∂‰∏îdirtyÂèò‰∏∫false
    }
    return this._value;
  }
  set value(newValue) {
    this.setter(newValue);
  }
}
