import { DirtyLevels } from "./constants";

export function effect(fn, options?) {
  // 1. ÂàõÂª∫effectÁöÑÊó∂ÂÄôÔºå‰ºöÂÖàÊâßË°å‰∏ÄÊ¨°fnÔºåÊ≠§Êó∂‰ºöËÆøÈóÆÂà∞ÂìçÂ∫îÂºèÊï∞ÊçÆÔºåËß¶Âèëget
  // 2. ÂΩìÂìçÂ∫îÂºèÊï∞ÊçÆÂèòÂåñÁöÑÊó∂ÂÄôÔºå‰ºöËß¶ÂèësetÔºåÊ≠§Êó∂‰ºöÊâßË°åeffectÁöÑrunÊñπÊ≥ï
  const _effect = new ReactiveEffect(fn, () => {
    _effect.run();
  });
  _effect.run();

  if (options) {
    Object.assign(_effect, options);
  }

  const runner = _effect.run.bind(_effect);
  runner.effect = _effect;
  return runner;
}
export let activeEffect;

function preCleanEffect(effect) {
  effect._depLength = 0;
  effect._trackId++; //ÊØèÊ¨°ÊâßË°åÔºåtrackId+1 ÔºåÂ¶ÇÊûúÂΩìÂâçÂêå‰∏Ä‰∏™effectÊâßË°åÔºåidÂ∞±ÊòØÁõ∏ÂêåÁöÑ
}

function postCleanEffect(effect) {
  if (effect.deps.length > effect._depLength) {
    for (let i = effect._depLength; i < effect.deps.length; i++) {
      cleanDepEffect(effect.deps[i], effect); //Âà†Èô§Êò†Â∞ÑË°®ÂØπÂ∫îÁöÑeffect
    }
    effect.deps.length = effect._depLength; //Êõ¥Êñ∞‰æùËµñÂàóË°®ÁöÑÈïøÂ∫¶
  }
}
export class ReactiveEffect {
  _trackId = 0; //ËÆ∞ÂΩïÂΩìÂâçÁöÑeffectÊâßË°å‰∫ÜÂá†Ê¨°
  deps = [];
  _depLength = 0;
  _running = 0;
  _dirtyLevel = DirtyLevels.Dirty;
  // ÈªòËÆ§ÊòØÂìçÂ∫îÂºèÁöÑ
  public active = true;
  // fnÁî®Êà∑ÁºñÂÜôÁöÑÂáΩÊï∞Ôºåscheduler(Êï∞ÊçÆÂèëÁîüÂèòÂåñË∞ÉÁî®run)Ë∞ÉÂ∫¶ÂáΩÊï∞
  constructor(public fn, public scheduler) {}
  public get dirty() {
    return this._dirtyLevel === DirtyLevels.Dirty;
  }
  public set dirty(value) {
    this._dirtyLevel = value ? DirtyLevels.Dirty : DirtyLevels.NoDirty;
  }
  run() {
    // ÊØèÊ¨°ËøêË°åeffectÂêéÂèò‰∏∫Nodirty
    this._dirtyLevel = DirtyLevels.NoDirty;
    if (!this.active) {
      // ‰∏çÊòØÊøÄÊ¥ªÁöÑÔºåÊâßË°åÂêé‰ªÄ‰πàÈÉΩ‰∏çÂÅö
      return this.fn();
    }
    let lastEffect = activeEffect;
    try {
      activeEffect = this;
      preCleanEffect(this);
      this._running++; //ËøêË°å‰∏ÄÊ¨°+1
      return this.fn(); //‰æùËµñÊî∂ÈõÜ
    } finally {
      this._running--; //ÁªìÊùü‰πãÂêé-1
      postCleanEffect(this);
      activeEffect = lastEffect;
    }
  }
  stop() {
    this.active = false;
  }
}
function cleanDepEffect(dep, effect) {
  dep.delete(effect);
  if (dep.size === 0) {
    dep.__cleanup__();
  }
}

// ÂèåÂêëËÆ∞ÂøÜ
// trackId Áî®‰∫éËÆ∞ÂΩïeffectÊâßË°å‰∫ÜÂá†Ê¨°ÔºàÈò≤Ê≠¢‰∏Ä‰∏™Â±ûÊÄßÂú®‰∏Ä‰∏™effect‰∏≠Â§öÊ¨°Êî∂ÈõÜ‰æùËµñÔºâÔºåÂ§öÊ¨°ËÆøÈóÆÂè™Êî∂ÈõÜ‰∏ÄÊ¨°
// depLength Áî®‰∫éËÆ∞ÂΩïeffectÂÖ≥ËÅî‰∫ÜÂá†‰∏™dep
export function trackEffect(effect, dep) {
  // ÈúÄË¶ÅÈáçÊñ∞ËÆæËÆ°Êî∂ÈõÜ‰æùËµñÔºåÊØèÊ¨°ÊâßË°åÁöÑÊó∂ÂÄôÔºåÈÉΩÈúÄË¶ÅÊ∏ÖÁ©∫
  if (dep.get(effect) !== effect._trackId) {
    dep.set(effect, effect._trackId);
    let oldDep = effect.deps[effect._depLength];
    if (oldDep !== dep) {
      if (oldDep) {
        cleanDepEffect(oldDep, effect);
      }
      effect.deps[effect._depLength++] = dep; //ÊåâÁÖßÊú¨Ê¨°ÊúÄÊñ∞ÁöÑÊù•Â≠òÊîæ
    } else {
      effect._depLength++;
    }
  }
  // dep.set(effect, effect._trackId);
  // // ÊàëËøòÊÉ≥effectÂíådepÂÖ≥ËÅîËµ∑Êù•
  // effect.deps[effect._depLength++] = dep;
  console.log("üöÄ ~ trackEffect ~ effect.deps:", effect, dep);
}

export function triggerEffects(dep) {
  for (const effect of dep.keys()) {
    if (effect._dirtyLevel === DirtyLevels.NoDirty) {
      effect._dirtyLevel = DirtyLevels.Dirty;
    }
    if (effect._running === 0) {
      // Èò≤Ê≠¢ÈáçÂ§çÊâßË°å =>Â¶ÇÊûú‰∏çÊòØÊ≠£Âú®ÊâßË°åÁöÑÔºåÊâçËÉΩÂ§üÁªßÁª≠ÊâßË°å
      if (effect.scheduler) {
        effect.scheduler();
      }
    }
  }
}
