import { activeEffect, trackEffect, triggerEffects } from "./effect";

const targetMap = new WeakMap();

export const createDep = (cleanup, key) => {
  const dep = new Map() as any; //ÂàõÂª∫ÁöÑÊî∂ÈõÜÂô®ËøòÊòØ‰∏Ä‰∏™map
  dep.__cleanup__ = cleanup; //Â¢ûÂä†Âà†Èô§ÁöÑÊñπÊ≥ï
  dep.name = key;
  return dep;
};

export function track(target, key) {
  // TODO
  // activeEffect  Â¶ÇÊûúÊúâËøô‰∏™ÂÄºÔºåËØ¥ÊòéÊòØÂú®effect‰∏≠Ë∞ÉÁî®ÁöÑÔºåÈúÄË¶ÅÊî∂ÈõÜ‰æùËµñ
  if (activeEffect) {
    let depsMap = targetMap.get(target);
    if (!depsMap) {
      //Êñ∞Â¢ûÁöÑ
      depsMap = new Map();
      targetMap.set(target, depsMap);
    }

    let dep = depsMap.get(key);
    if (!dep) {
      dep = createDep(() => depsMap.delete(key), key); //ÂêéÊúüÊ∏ÖÁêÜ‰∏Ä‰∫õ‰∏çÈúÄË¶ÅÁöÑÂ±ûÊÄß
      depsMap.set(key, dep);
    }

    trackEffect(activeEffect, dep); //Â∞ÜÂΩìÂâçÁöÑeffectÊîæÂÖ•Âà∞depÔºàÊò†Â∞ÑË°®Ôºâ‰∏≠ÔºåÂêéÁª≠ÂèØ‰ª•Ê†πÊçÆÂÄºÁöÑÂèòÂåñËß¶ÂèëÊ≠§dep‰∏≠Â≠òÊîæÁöÑeffect
    console.log("üöÄ ~ track ~ targetMap:", targetMap);
  }
}

export function trigger(target, key, newValue, oldValue) {
  let depsMap = targetMap.get(target);
  if (!depsMap) return;
  let dep = depsMap.get(key);
  if (dep) {
    //Ëøô‰∏™‰øÆÊîπÁöÑÂ±ûÊÄßÂØπÂ∫îÁöÑeffect
    triggerEffects(dep);
  }
}

